@import "mixins";
@media screen and (max-width:1200px) {
  h1, h4, h5, p, a, i, button, span, address, li, label, small {
    font-size: 15px; }
  h2, h3 {
    font-size: 2em; }
  .h1 {
    font-size: 1.8em; }
  .h2 {
    font-size: 1.3em; }
  .group {
    margin-bottom: 20px; }
  .button-form {
    font-size: .9em; }
  .globalnav {
    &__logo {
      width: 95px; } }
  .head-offer {
    &__ball {
      height: 9vh; } }
  .half-form {
    &__half-ball {
      width: 25px; }
    &__inform-first {
      padding: 35px 40px; }
    .political {
      margin-bottom: 20px; } }
  .learn {
    &__form-first {
      padding: 25px; } }
  .contacts {
    padding: 70px 0; }
  .footer {
    &__logo {
      width: 75px; } } }
@media screen and (max-width:992px) {
  h1, h4, h5, p, a, i, button, span, address, li, label, small {
    font-size: 13px; }
  .globalnav {
    &__logo {
      width: 75px; }
    &__text-logo {
      font-size: 0.67em; }
    &__contact {
      padding: 7px 17px; } } }
@media screen and (max-width:768px) {}

@media screen and (max-width:575.98px) {}
